.maincontent{
    margin-top: 100px;
    margin-left: 150px;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    
}
.infocontainer{
    margin: 5rem ;
}
.header{
    color: white;
}
.listContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0;
    list-style: none;
}
.ul{
    padding-left: 0;
}

.list:before {
        border-style: solid;
        border-width: 0.10em 0.10em 0em 0em;
        border-color: #1bf8f8;
        content: '';
        display: inline-block;
        height: 0.45em;
        left: 0.15em;
        position: relative;
        top: 0.3em;
        transform: rotate(-45deg);
        vertical-align: top;
        width: 0.45em;
        left: 0;
        transform: rotate(45deg);
        margin-right: 20px;
        
}
.line{
    position: absolute;
    background: #1bf8f8;
    height: 0.2rem;
    width: 16rem;
    left: 28rem;
    top: 14.4rem;
}