.maincontent{
    margin-top: 100px;
    margin-left: 150px;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    
}
.infocontainer{
    margin: 15rem ;
    
}
.info{
    font-size: 1.8rem;
    text-align: center;
    
}
.name{
    font-size: 2.5rem;
    color: #1bf8f8;
    
}
.picbox{
    position: relative;
    width: fit-content;
    height: fit-content;
    top: 5rem;
    
}
.pic{
    width: 26rem;
    border-radius: 3%;
    
}
.picBorder{
    position: absolute;
    border: 2px solid #1bf8f8;
    top: 0.6rem;
    left: 0.6rem;
    border-radius: 3%;
    height: 26rem;
    width: 26rem;
    clip-path: polygon(90% 0, 100% 0, 100% 100%, 0 100%, 0 90%);
    z-index: -1;
}