body{
    background-color: #20272f;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    color: grey;
    margin: 0;
    padding: 0;
}
.aqua{
    color: #1bf8f8;
}
.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #171c22;
    display: flex;
    justify-content: space-between;
    border-top: 0px;
    border-left: 0px;
    border-bottom: 15px; 
    border-image: linear-gradient(#023030, #13aaaa) 50;
    border-style: solid;
}
.index{
    display: flex;
}
.indx{
    display: flex;
    align-items: center;
    padding: 1rem;
}
a{
    text-decoration: none;
    color: #fbffff;
}
.myname{
    
    color: #fdfdfd;
    border: 1px solid #1bf8f8;
    border-radius: 4px;
    margin: 10px 15px 10px 20px;
    padding: 10px 12px;
}
                    /* END OF NAVBAR */


.maincontent{
    margin-top: 100px;
    margin-left: 150px;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    
}
.infocontainer{
    margin: 5rem ;
}
.header{
    color: white;
}
.line{
    position: absolute;
    background: #1bf8f8;
    height: 0.2rem;
    width: 16rem;
    left: 18rem;
    top: 9.2rem;
}



